@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz@6..96&family=Poppins:wght@300&display=swap");

* {
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
}
:root {
  --primary-color: #23b4c2;
  --secondary-color: white;
}
body {
  background-color: var(--primary-color);
}
html {
  font-size: 75.5%;
}

.header {
  width: 100%;
  height: 450px;
  color: var(--secondary-color);
  align-items: center;
  position: relative;
}
.header ::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.row2 {
  position: absolute;
  top: 100px;
  right: 14%;
  text-align: center;
}
.row2 h2 {
  font-size: 5rem;
  margin-top: 20px;
  margin-bottom: 15px;
}
.row2 img {
  margin-top: 30px;
  width: 250px;
}
.row2 h3 {
  font-size: 1.7rem;
  text-align: center;
  margin-left: 35px;
  margin-bottom: 25px;
  font-family: "Bodoni Moda", serif;
}

.row1 img {
  width: 100%;
  height: 450px;
  object-fit: cover;
}
.search {
  align-items: center;
  justify-content: center;
}
.search input {
  outline: 0;
  border: 0;
  width: 100%;
  height: 45px;
  padding: 0 1.5rem;
  font-size: 15px;
  font-weight: bold;
  border-radius: 1.5rem;
  box-shadow: 3px 5px 5px rgba(88, 85, 80, 0.2);
}

.container {
  width: 65%;
  margin: 0 auto;
  margin-top: 25px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 0.5fr));
  grid-gap: 22px;
  box-sizing: border-box;
}

.card {
  background-color: var(--secondary-color);
  border-radius: 1rem;
  padding: 0.5rem;
  margin-bottom: 10px;
  box-shadow: 3px 5px 5px rgba(88, 85, 80, 0.8);
  text-align: center;
  position: relative;
  transition: .5s;
}
.card:hover {
  color: #5BBE74;
  transform: scale(1.1);
  cursor: pointer;
}
.card img {
  width: 100%;
  height: 245px;
  border-radius: 1rem 1rem 0 0;
}
.card .bottom {
  display: flex;
  flex-direction: column;
}
.card .bottom .title {
  font-size: 14px;
  margin-top: 20px;
  margin-bottom: 30px;
  align-items: center;
}
.card .bottom .amount {
  position: absolute;
  bottom: 10px;
  left: 10px;
  right: 10px;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  font-size: 13px;
  font-weight: bold;
  padding: 0.2rem;
  border-radius: 20px;
}

.overlay {
  min-height: 100vh;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.1));
  display: flex;
  align-items: center;
  justify-content: center;
}
.overlay-inner {
  background: white;
  width: 700px;
  height: 450px;
  padding: 1.5rem;
  position: relative;
  box-sizing: border-box;
  overflow: auto;
  font-size: 1.3rem;
  border-radius: 20px 0 0 20px;
}
.overlay-inner .close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  outline: none;
  border:none;
  border-radius: 180%;
  padding: 0px 12px 0px 12px;
  font-size: 25px;
  background-color: gainsboro;
   transition: .4s;

}
.overlay-inner .close:hover{
  transform: scale(1.1);
  color: red;
  cursor: pointer;
}
.overlay-inner .inner-box {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  
}
.overlay-inner .inner-box img {
  margin-right: 30px;
  width: 150px;
  height: 200px;
  cursor: pointer;
  transition: .5s;
  border-radius: 15px;
  box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
}
.overlay-inner .inner-box img:hover{
transform: scale(1.1);
}
.overlay-inner .inner-box h3 {
  margin-top: 10px;
  color: green;
}
.overlay-inner .inner-box h4 {
  color: rgb(51, 51, 228);
}
.overlay-inner .inner-box button {
  outline: none;
  border: none;
  width: 100px;
  border-radius: 5px;
  background-color: rgb(222, 117, 36);
  color: white;
  font-weight: bold;
  padding: 0.3rem 0.5rem;
  cursor: pointer;
  transition: .5s;
}
.overlay-inner .inner-box button:hover{
transform: scale(1.1);
}

.overlay-inner .description {
  margin-top: 3rem;
  text-align: justify;
  font-size: 13px;
}
.lined{
  width: 100%;
  height: 10px;
  margin-bottom: 50px;
  background-color: #53A0B8;
}
